html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

#root {
  width: 100%;
  height: 100%;
}
.App {
  background-color: #eae0d6;
  width: 100%;
  height: 100%;;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.App .content {
  flex: 1 1;
  top: 0px;
  bottom: 0px;
  position: absolute;
  width: 100%;
  overflow: auto;
  display: flex;
}


.userlist {
  position: absolute;
  top: 0px;
  bottom: 35px;
  right: 0px;
  background-color: #cdb7a0;
  z-index: 100;
  transition: background-color 2s width 2s left 2s;
}

.userlist.close {
  width: 45px;
  transition: width 2s;
}

.userlist.open {
  width: 300px;
  transition: width 2s;
}

.userlist .close-button {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14pt;
  background-color: azure;
  border: gray 1px solid;
  border-radius: 50%;
  float: left;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

}



.userlist .close-button:hover {
  background-color: #cee740;
}

