.tabbar {
  height: 100%;
  background-color: #cdb7a0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  top: 0px;
  bottom: 0px;
  width: 100%;
}

.tabbar .tabbutton {
  display: inline-block;
  text-align: center;
  font-size: 1.5em;
  color: white;
  margin: 0 5px;
  padding: 10px;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 1s;
}
.tabbar .tabbutton1 {
  display: inline-block;
  flex-direction: column;
  text-align: center;
  font-size: 1.5em;
  color: rgb(255, 255, 255);
  margin: 0 5px;
  padding: 10px;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 1s;
  top: 50px;
}

.tabbar .tabbutton:hover {
  background-color: #8a6b33;;
}

.tabbar.stick-bottom {
  position: absolute;
  bottom: 0px;
  width: 100%;
}

.tabbar.stick-top {
  position: absolute;
  top: 0px;
  width: 100%;
}

.tabbar.stick-left {
  position: absolute;
  left: 0px;
  height: 100%;
  flex-direction: column;
}

.tabbar.stick-right {
  position: absolute;
  right: 0px;
  height: 100%;
  flex-direction: column;
}
.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: solid 1px gray;
  overflow: hidden;
}

.avatar.small {
  width: 40px;
  height: 40px;
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.chatbox {
  flex: 1 1;
  background-color: silver;
  overflow: auto;
}

.chatbox-item{
  transition: top 1s opacity 1s display 1s width 1s height 1s;
}

.chatbox-item.left{
  text-align: left;
  margin-left: 24px;
}

.chatbox-item.right{
  text-align: right;
  margin-right: 70px;
}

.chatbox-item .avatar{
  display: inline-block;
}

.titlebar {
  height: 50px;
  background-color: #89796a;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  color: aliceblue;
}
.chat {
  position: relative;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;

  flex:4 1;
}



.inputbox {
  background-color: #eae0d7;
  display: flex;
  height: 50px;
  width:97%;
}

.inputbox .label1 {
  font-size: 1.5em;
  text-align: right;
}

.inputbox .text {
  font-size: 1em;
  padding-left: 8px;
  padding-right: 8px;
  flex: 1 1;
}

.inputbox .button {
  font-size: 1em;
  min-width: 40px;
}
.Profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: #eae0d7;
  padding: 0;
  margin: 0;
}
.friend-list {
  flex:1 1
}

.friend-list-item {
  display: flex;
  align-items: center;
  padding: 0.5rem;
  border-bottom: 1px solid #e5e5e5;
  cursor: pointer;
}
